export default {
  props: {
    color: {
      type: String,
      required: true
    },
    minHeight: {
      type: String,
      default: "100"
    }
  },
  data() {
    return {
      show: false
    };
  },
  computed: {
    cardMinHeight: function() {
      return this.minHeight + "px";
    }
  }
};
